                    :index='idx'
                    :programme='item'
                )
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        programmeItem: () => import("../../scholar/programmes/programme-item")
    },
    created() {
        this.fetchScholarProgrammes(this.user.id)
    },
    computed: {
        ...mapState ("user", {
            user: state => state.user
        }),
    ...mapState ("programmes", {
           programmes: state => state.programmes
        })
    },
    methods: {
        ...mapActions("programmes", ["fetchScholarProgrammes"])
    }
}
</script>
